.logo {
  background-color: #343434;
  height: 100vh;
}
.conteiner {
  max-width: 420px;
  margin: 0 auto;
}
.logo_tesla {
  margin: 0 auto;
}

.logo_money {
  position: absolute;
  top: 0;
  transform: translate(0px, 63px);
}
.wrapper {
  position: relative;
}
.logo_color {
  position: absolute;
  top: 0;
  transform: translate(62px, 221px);
  z-index: 1;
}
.logo_circle {
  position: absolute;
  top: 0;
  transform: translate(110px, 285px);
}
.logo_circle2 {
  position: absolute;
  top: 0;
  transform: translate(61px, 238px);
}
.logo_circle3 {
  position: absolute;
  top: 0;
  transform: translate(68px, 179px);
}
.logo_car {
  position: absolute;
  top: 0;
  transform: translate(206px, 290px);
}
.logo_gold-tesla {
  position: absolute;
  top: 0;
  transform: translate(0, 317px);
}
.logo_mask {
  position: absolute;
  top: 0;
  z-index: 2;
  transform: translate(56px, 159px);
}
.logo_money2 {
  position: absolute;
  top: 0;
  transform: translate(287px, 137px);
}
.logo_money3 {
