.logo_title {
  font-style: normal;
  font-weight: 400;
  font-size: 26px;
  line-height: 1.19;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  width: 323px;
  height: 124px;
  position: absolute;
  transform: translate(50px, 484px);
  top: 0;
}
.logo_span {
  color: rgba(237, 185, 85, 1);
}
.logo_text {
  display: block;
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 1.17;
  text-align: center;
  color: #ffffff;
  position: absolute;
  top: 0;
  transform: translate(33px, 661px);
  width: 338px;
}
.logo_button {
  padding: 20px 60px;
  background: linear-gradient(92.27deg, #f7cea9 0%, #edb955 100%);
  box-shadow: 0px 0px 41.6949px rgba(239, 189, 98, 0.35);
  border-radius: 6.94915px;
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 1;

  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  color: #5e3c2f;
  position: absolute;
  top: 0;
  transform: translate(48px, 745px);
  border: none;
}
